import React, { Component } from 'react'
import PureRenderMixin from 'react-addons-pure-render-mixin'
import Star from '@/components/Star'

export default class CommentIndex extends Component {
  constructor () {
    super()
    this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate.bind(this);
    this.state = {
      commentTit : [
        {name:'沙龙内容',stars:1},
        {name:'互动氛围',stars:2},
        {name:'场地环境',stars:3},
        {name:'营业部服务',stars:4}
      ]
    }
    this.getStarState = this.getStarState.bind(this)
  }
  getStarState = (index,num) =>{
    console.log(index,num);
  }
  render() {
    let { commentTit } = this.state
    return (
      <div className="comment-container">
        <div className="comment-tit">大数据教你精准把握大势机会</div>
        <textarea className="comment-input" placeholder="活动还满意吗？说说您参加本次沙龙的心得及反馈，我们将及时收集您给的意见并做出优化！"></textarea>
        <div className="com_tit com_titorange">沙龙评分</div>
        <ul className="comment-star">
          {
            commentTit.map((item,index) =>{
              return (
                <li key={index}>
                  <span className="star-lefttit">{item.name}</span>
                  <Star callback={this.getStarState.bind(this,index)} stars={item.stars} />
                </li>
              )
            })
          }
        </ul>
        <div className="comment-titright">您的意见将是我们未来的方向</div>
        <a href="#" className="btn-blue">提交</a>
      </div>
    )
  }
}
